,attrs:{"data-aos":"fade-up"}},[_vm._v("La dirección, vigilancia y control de los riesgos laborales se encuentra a cargo del Consejo Nacional de Riesgos Laborales del Ministerio de Trabajo. La administración está a cargo de las Administradoras de Riesgos Laborales. El Consejo Nacional de Riesgos Laborales es un organismo de carácter permanente, adscrito al Ministerio de Trabajo. Tiene una Secretaría Técnica cuyas funciones le han sido asignadas a la Dirección General de Riesgos Laborales del Ministerio de Trabajo. La Secretaría Técnica tiene a su cargo la presentación de los estudios técnicos y proyectos destinados a la protección de los riesgos profesionales.")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('figure',{staticClass:"mt-5"},[_c('div',{staticClass:"video"},[_c('iframe',{attrs:{"width":"560","height":"315","src":"https://www.youtube.com/embed/FEAvgOgNGKw","title":"Prestaciones otorgadas por el Sistema General de Riesgos Laborales","frameborder":"0","allow":"accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture","allowfullscreen":""}})])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-segundo"},[_c('h2',{attrs:{"id":"t_1_8"}},[_vm._v("1.8 Estructura")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row"},[_c('div',{staticClass:"col-lg-8 mb-4 mb-lg-0",staticStyle:{"place-self":"center"}},[_c('p',{attrs:{"data-aos":"fade-up"}},[_vm._v("El empleador, el trabajador y la administradora de riesgos laborales son las partes más importantes de la estructura del Sistema de Riesgos Laborales; cada uno tiene sus obligaciones. El empleador a la par de contratar un trabajador, realiza su afiliación al sistema de seguridad social, debe responder por el pago de la misma, informa y forma a su colaborador en materia de riesgos laborales y atiende de manera oportuna toda la temática del Sgsst; por otra parte, el trabajador está obligado a cumplir la totalidad de las responsabilidades en el Sgsst; finalmente la ARL cumple a cabalidad la función que le atañe frente a la administración de los riesgos identificados en la empresa que le ha contratado. A continuación, se presenta un esquema de todos los actores del Sistema General de Riesgos Laborales:")])]),_c('div',{staticClass:"col-lg-4",staticStyle:{"place-self":"center"}},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/25.png"),"data-aos":"fade-left"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('figure',{staticClass:"mt-5"},[_c('img',{attrs:{"src":require("@/assets/curso/tema1/26.svg"),"data-aos":"zoom-in-up"}})])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mt-5"},[_c('div',{staticClass:"col-lg-1 col-md-3 mb-4 mb-md-0",staticStyle:{"place-self":"center"}},[_c('figure',{staticClass:"col-6 m-auto"},[_c('img',{attrs:{"src":require("@/assets/curso/tema1/27.svg"),"data-aos":"fade-right"}})])]),_c('div',{staticClass:"col-lg-11 col-md-9",staticStyle:{"place-self":"center"}},[_c('p',{staticClass:"mb-0",attrs:{"data-aos":"fade-up"}},[_vm._v("Los "),_c('strong',[_vm._v("ATEL (Accidente de Trabajo, Enfermedad Laboral)")]),_vm._v(" son parte de la vida laboral y aunque se reducen al máximo mientras se trabaje de buena forma la prevención desde en salud y seguridad en el trabajo son probables y por eso la ley deja claro que cada caso de estos será trabajado de buena forma y por supuesto que el trabajador seguirá siendo protegido y seguirá recibiendo su salario siempre y cuando el contrato de trabajo sea legal tal como lo puede ver a continuación salvaguardando siempre al trabajador:")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mt-5"},[_c('div',{staticClass:"col-lg-4 mb-4 mb-lg-0",sta